
import React, { useState, useEffect, useCallback } from 'react';
import { ethers } from 'ethers';
import './styles/App.css';
import ArbitrationSystemABI from './contracts/ArbitrationSystem.json';
import GrullTokenABI from './contracts/GrullToken.json';
import JurorDashboard from './components/JurorDashboard';
import DisputeCreation from './components/DisputeCreation';
import DisputeList from './components/DisputeList';
import DisputeDetails from './components/DisputeDetails';
import Header from './components/Header';
import { ARBITRATION_SYSTEM_ADDRESS, GRULL_TOKEN_ADDRESS } from './config';

function App() {
  // eslint-disable-next-line no-unused-vars
  const [_provider, setProvider] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [_signer, setSigner] = useState(null);
  const [account, setAccount] = useState('');
  const [arbitrationSystem, setArbitrationSystem] = useState(null);
  const [grullToken, setGrullToken] = useState(null);
  const [jurorInfo, setJurorInfo] = useState(null);
  const [disputes, setDisputes] = useState([]);
  const [selectedDispute, setSelectedDispute] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [tokenBalance, setTokenBalance] = useState('0');
  const [activeView, setActiveView] = useState('dashboard');
  
  // Use useCallback to memoize the loadDisputes function
  const loadDisputes = useCallback(async (contract) => {
    try {
      // This is a simplified approach - in a real app, you'd use events or a backend
      const disputeCount = await contract.disputeCount();
      const loadedDisputes = [];
      
      for (let i = 0; i < disputeCount; i++) {
        const dispute = await contract.disputes(i);
        
        // Check if user is involved in this dispute
        const isCreator = dispute.creator.toLowerCase() === account.toLowerCase();
        const isRespondent = dispute.respondent.toLowerCase() === account.toLowerCase();
        
        // Check if user is a juror for this dispute
        let isJuror = false;
        if (dispute.jurorSelectionComplete) {
          const selectedJurors = await contract.getSelectedJurors(i);
          isJuror = selectedJurors.some(j => j.toLowerCase() === account.toLowerCase());
        }
        
        if (isCreator || isRespondent || isJuror) {
          loadedDisputes.push({
            id: i,
            creator: dispute.creator,
            respondent: dispute.respondent,
            description: dispute.description,
            fee: ethers.utils.formatEther(dispute.fee),
            resolved: dispute.resolved,
            jurorCount: dispute.jurorCount.toNumber(),
            votesForCreator: dispute.votesForCreator.toNumber(),
            votesForRespondent: dispute.votesForRespondent.toNumber(),
            jurorSelectionComplete: dispute.jurorSelectionComplete,
            isCreator,
            isRespondent,
            isJuror
          });
        }
      }
      
      setDisputes(loadedDisputes);
    } catch (error) {
      console.error('Error loading disputes:', error);
      setError('Failed to load disputes: ' + error.message);
    }
  }, [account]); // Added account as a dependency since it's used in the function

  useEffect(() => {
    const init = async () => {
      try {
        if (window.ethereum) {
          const provider = new ethers.providers.Web3Provider(window.ethereum);
          setProvider(provider);
          
          const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
          const account = accounts[0];
          setAccount(account);
          
          const signer = provider.getSigner();
          setSigner(signer);
          
          const arbitrationSystem = new ethers.Contract(
            ARBITRATION_SYSTEM_ADDRESS,
            ArbitrationSystemABI.abi,
            signer
          );
          setArbitrationSystem(arbitrationSystem);
          
          const grullToken = new ethers.Contract(
            GRULL_TOKEN_ADDRESS,
            GrullTokenABI.abi,
            signer
          );
          setGrullToken(grullToken);
          
          // Load juror info
          const jurorInfo = await arbitrationSystem.jurors(account);
          setJurorInfo({
            stakedAmount: ethers.utils.formatEther(jurorInfo.stakedAmount),
            lastActivityTimestamp: new Date(jurorInfo.lastActivityTimestamp.toNumber() * 1000),
            reputation: jurorInfo.reputation.toNumber(),
            isActive: jurorInfo.isActive
          });
          
          // Load token balance
          const balance = await grullToken.balanceOf(account);
          setTokenBalance(ethers.utils.formatEther(balance));
          
          // Load disputes
          await loadDisputes(arbitrationSystem);
          
          setLoading(false);
        } else {
          setError('Please install MetaMask to use this application');
          setLoading(false);
        }
      } catch (error) {
        console.error('Initialization error:', error);
        setError('Failed to initialize application: ' + error.message);
        setLoading(false);
      }
    };
    
    init();
    
    // Setup event listeners for MetaMask
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', (accounts) => {
        window.location.reload();
      });
      
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      });
    }
    
    return () => {
      if (window.ethereum) {
        window.ethereum.removeAllListeners();
      }
    };
  }, [loadDisputes]); // Added loadDisputes as a dependency
  
  const refreshData = async () => {
    setLoading(true);
    try {
      // Refresh juror info
      const jurorInfo = await arbitrationSystem.jurors(account);
      setJurorInfo({
        stakedAmount: ethers.utils.formatEther(jurorInfo.stakedAmount),
        lastActivityTimestamp: new Date(jurorInfo.lastActivityTimestamp.toNumber() * 1000),
        reputation: jurorInfo.reputation.toNumber(),
        isActive: jurorInfo.isActive
      });
      
      // Refresh token balance
      const balance = await grullToken.balanceOf(account);
      setTokenBalance(ethers.utils.formatEther(balance));
      
      // Refresh disputes
      await loadDisputes(arbitrationSystem);
      
      // If a dispute was selected, refresh its data
      if (selectedDispute !== null) {
        const dispute = await arbitrationSystem.disputes(selectedDispute.id);
        const selectedJurors = await arbitrationSystem.getSelectedJurors(selectedDispute.id);
        
        setSelectedDispute({
          ...selectedDispute,
          votesForCreator: dispute.votesForCreator.toNumber(),
          votesForRespondent: dispute.votesForRespondent.toNumber(),
          resolved: dispute.resolved,
          selectedJurors
        });
      }
      
      setLoading(false);
    } catch (error) {
      console.error('Error refreshing data:', error);
      setError('Failed to refresh data: ' + error.message);
      setLoading(false);
    }
  };
  
  const handleDisputeSelect = async (disputeId) => {
    setLoading(true);
    try {
      const dispute = disputes.find(d => d.id === disputeId);
      
      if (dispute && dispute.jurorSelectionComplete) {
        const selectedJurors = await arbitrationSystem.getSelectedJurors(disputeId);
        dispute.selectedJurors = selectedJurors;
      }
      
      setSelectedDispute(dispute);
      setActiveView('disputeDetails');
      setLoading(false);
    } catch (error) {
      console.error('Error selecting dispute:', error);
      setError('Failed to load dispute details: ' + error.message);
      setLoading(false);
    }
  };
  
  const handleCreateDispute = async (respondentAddress, description, fee) => {
    setLoading(true);
    try {
      const tx = await arbitrationSystem.createDispute(
        respondentAddress,
        description,
        { value: ethers.utils.parseEther(fee) }
      );
      
      await tx.wait();
      await refreshData();
      setActiveView('disputes');
      setLoading(false);
    } catch (error) {
      console.error('Error creating dispute:', error);
      setError('Failed to create dispute: ' + error.message);
      setLoading(false);
    }
  };
  
  const handleStakeTokens = async (amount) => {
    setLoading(true);
    try {
      // First approve the arbitration system to spend tokens
      const approveTx = await grullToken.approve(
        ARBITRATION_SYSTEM_ADDRESS,
        ethers.utils.parseEther(amount)
      );
      await approveTx.wait();
      
      // Then stake the tokens
      const stakeTx = await arbitrationSystem.stakeTokens(
        ethers.utils.parseEther(amount)
      );
      await stakeTx.wait();
      
      await refreshData();
      setLoading(false);
    } catch (error) {
      console.error('Error staking tokens:', error);
      setError('Failed to stake tokens: ' + error.message);
      setLoading(false);
    }
  };
  
  const handleUnstakeTokens = async (amount) => {
    setLoading(true);
    try {
      const tx = await arbitrationSystem.unstakeTokens(
        ethers.utils.parseEther(amount)
      );
      await tx.wait();
      
      await refreshData();
      setLoading(false);
    } catch (error) {
      console.error('Error unstaking tokens:', error);
      setError('Failed to unstake tokens: ' + error.message);
      setLoading(false);
    }
  };
  
  const handleCastVote = async (disputeId, voteForCreator) => {
    setLoading(true);
    try {
      const tx = await arbitrationSystem.castVote(disputeId, voteForCreator);
      await tx.wait();
      
      await refreshData();
      setLoading(false);
    } catch (error) {
      console.error('Error casting vote:', error);
      setError('Failed to cast vote: ' + error.message);
      setLoading(false);
    }
  };
  
  const renderContent = () => {
    if (loading) {
      return <div className="loading">Loading...</div>;
    }
    
    if (error) {
      return <div className="error">{error}</div>;
    }
    
    switch (activeView) {
      case 'dashboard':
        return (
          <JurorDashboard 
            account={account}
            jurorInfo={jurorInfo}
            tokenBalance={tokenBalance}
            onStake={handleStakeTokens}
            onUnstake={handleUnstakeTokens}
          />
        );
      case 'createDispute':
        return (
          <DisputeCreation 
            onCreateDispute={handleCreateDispute}
          />
        );
      case 'disputes':
        return (
          <DisputeList 
            disputes={disputes}
            onSelectDispute={handleDisputeSelect}
          />
        );
      case 'disputeDetails':
        return (
          <DisputeDetails 
            dispute={selectedDispute}
            account={account}
            onCastVote={handleCastVote}
            onBack={() => setActiveView('disputes')}
          />
        );
      default:
        return <div>Unknown view</div>;
    }
  };
  
  return (
    <div className="app">
      <Header 
        account={account}
        tokenBalance={tokenBalance}
        onNavigate={setActiveView}
        currentView={activeView}
      />
      <main className="content">
        {renderContent()}
      </main>
    </div>
  );
}

export default App;
